import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { TemplateModel } from '../models/template.model';
import { ClassicOneComponent } from './classic-one/classic-one.component';
import { ClassicTwoComponent } from './classic-two/classic-two.component';

@Component({
    selector: 'app-templates',
    templateUrl: './templates.component.html',
    styleUrls: ['./templates.component.scss']
})
export class TemplatesComponent implements OnInit {

    public templates: TemplateModel[] = [
        {
            id: 'classic-one',
            component: ClassicOneComponent,
            name: 'Classic One',
            format: 'classic',
            thumbnail: '/assets/resumes/classic-one.png'
        },
        {
            id: 'classic-two',
            component: ClassicTwoComponent,
            name: 'Classic Two',
            format: 'classic',
            thumbnail: '/assets/resumes/classic-two.png'
        }
    ];

    public selectedTemplate: string;

    @Output()
    public templateSelected = new EventEmitter();

    constructor() { }

    ngOnInit(): void {
        // Always select first template
        this.templateSelected.emit(this.templates[0]);
    }

    public selectTemplate(template: TemplateModel): void {
        this.selectedTemplate = template.id;
        this.templateSelected.emit(template);
    }

}
